.Conteiner{
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    
}
.ConteinerL{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    img{
        width: 69px;
        height: 100%;
    };

}
.DoubleConteiner{
    width: 100%;
    display: flex;
    flex-direction: column;
    .Conteiner{
        justify-content: start;
    }
}
.ButtonDiv{
    
    background: #FFAE33;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    margin-top: 20px;
    margin-bottom: 10px;
}

